<template>
     <div>
     <el-button type="primary" icon="el-icon-upload" @click="searchall()">查询</el-button>
     <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="id"
      label="病历号"
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="180">
    </el-table-column>
    <el-table-column
      prop="sex"
      label="性别">
    </el-table-column>
    <el-table-column
      prop="birthdate"
      label="生日">
    </el-table-column>
    <el-table-column
      prop="type"
      label="挂号类型">
    </el-table-column>
    <el-table-column
      prop="doctor"
      label="主治医生">
    </el-table-column>
    <el-table-column
      prop="fee"
      label="费用">
    </el-table-column>
    <el-table-column
      prop="time"
      label="挂号日期">
    </el-table-column>
  </el-table>
     </div>
</template>


<script>
     export default {
       data() {
      return { 
          tableData:{}
        }
      },
      methods: {
          searchall(){
              this.$axios.post('/registerfind')
            .then(response=>{
            console.log(response);
            if(response.status==200)
            {
                alert("查询成功");
                 var obj = JSON.parse(response.data);
                    //this.list = obj;
                    this.tableData = [obj];
                    //this.list.push(response.data);
                    console.log(this.tableData);
            }
            else
            {
              alert("查询失败");
            }
        })
        .catch(function (error) {
            console.log(error);
        }); 
          }    
    }  
}
</script>

<style>
</style>


