<template>
     <div>
     <v-header v-if="flag"></v-header>
     {{msg}}
     <input type='text' v-model='msg'>
     <button @click="getMsg()">获取数据</button>
     <button @click="goTodemo1()">跳转demo1</button>
     <button @click="flag=!flag">组件变化</button>
     <button @click="msg1++">刷新数据</button>
     </div>
</template>

<script>
import Header from './header.vue'
     export default {
       name: 'HelloWorld',
       data () {
          return {
          msg: '我是你爸爸',
          flag:true
          }
     },
     components:
     {
         'v-header':Header
     },
     methods:
     {
         getMsg()
         {
             alert(this.msg);
         },
         goTodemo1(){
             this.$router.push('/demo1')
         }
     }
}
</script>

<style>
