<template>
     <div>
     <v-menuhead></v-menuhead>
  <el-container>
<el-container>
 <el-aside width="200px" style="background-color: rgb(249, 249, 250);">
  <el-col :span="24">
    <el-menu
      default-active="2"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose">
      <el-menu-item index="1" @click="gotomess()">挂号</el-menu-item>
      <el-menu-item index="2">已挂号查询</el-menu-item>        
    </el-menu>
  </el-col>
  </el-aside>
     <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
  <el-form-item label="姓名">
    <el-input v-model="formLabelAlign.name"></el-input>
    </el-form-item>
    <el-form-item label="出生日期">
    <el-date-picker  v-model="formLabelAlign.date1" type="date"  placeholder="选择日期">
    </el-date-picker>
    </el-form-item>
    <el-form-item label="姓别">
  <el-radio v-model="formLabelAlign.sex" label="1">男</el-radio>
  <el-radio v-model="formLabelAlign.sex" label="2">女</el-radio>
  </el-form-item>
  <el-form-item label="科室">
    <el-select v-model="formLabelAlign.type" placeholder="请选择">
      <el-option value="1" label="外科"></el-option>
      <el-option value="2" label="内科"></el-option> 
      <el-option value="3" label="儿科"></el-option> 
      <el-option value="4" label="耳鼻喉科"></el-option> 
      <el-option value="5" label="皮肤科"></el-option> 
    </el-select>
    </el-form-item>
     <el-form-item label="挂号日期">
      <el-date-picker type="date" placeholder="选择日期" v-model="formLabelAlign.date2" style="width: 100%;"></el-date-picker>
  </el-form-item>
  <el-form-item label="挂号费">
    <el-input v-model="formLabelAlign.money"></el-input>
    </el-form-item>
    <el-form-item>
                <el-button type="primary" icon="el-icon-upload" @click="login()">登录</el-button>
    </el-form-item>
</el-form>

  </el-container>
</el-container>
     </div>
</template>


<script>
import menuhead from './menuhead.vue'
     export default {
       data() {
      return {
          radio:'1',
        labelPosition: 'right',
        formLabelAlign: {
          name: '',
          date1:'',
          sex: '1',
          type: '1',
          date2:'',
          money:''         
        }
      };
    },
     components:
     {'v-menuhead':menuhead},
      methods: {
      
      login()
      { 
            this.$axios.post('/register', {
            name: this.formLabelAlign.name,
            birthdate: this.formLabelAlign.date1,
            sex: this.formLabelAlign.sex,
            type: this.formLabelAlign.type,
            time: this.formLabelAlign.date2,
            fee: parseInt(this.formLabelAlign.money),
            doctor:"陈奕迅"
        })
        .then(response=>{
            console.log(response);
            if(response.data.code==200)
            {
              this.$router.push({
                path:'/index',
                query:{name:this.logInfo.name}
                });
            }
            else
            {
              alert("登录失败");
            }
        })
        .catch(function (error) {
            console.log(error);
        }); 
          }         
    }  
}
</script>

<style>

     .el-menu{
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 500px;
  }
  
  body > .el-container {
    margin-bottom: 20px;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
</style>


