<template>
  <div id="app">
  <h2>请求数据</h2>
  <button @click="getNews()">获取数据</button>
  <ul>
  <li v-for="item in list">{{item.title}}</li>
  </ul>
  <el-input v-model="input" placeholder="请输入内容" clearable></el-input>
  </div>
</template>

<script>
const axios = require('axios');
export default {
    data(){
        return{
            list:[],
            input:''
        }
    },
    methods: {
        getNews(){
            axios.get('http://www.phonegap100.com/appapi.php?a=getPortalList&catid=1&page=1')
            .then(response=> {
                // handle success
                console.log(response);
                this.list = response.data.result
            })
            .catch(error=> {
                // handle error
                console.log(error);
            })
            .then(function () {
                // always executed
            });
        }
    },
}
</script>

<style>

</style>



